
import { Component, OnInit, EventEmitter, NgZone } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/Rx'
import { PouchDBService } from './services/pouchdb.config'
import * as io from 'socket.io-client';
import * as $ from 'jquery';
import {NotificationsService, SimpleNotificationsComponent} from 'angular2-notifications';



declare var vis: any;
declare var swal:any
declare var changes:any

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  
})

export class AppComponent implements OnInit {
  
 
socket:any
open:boolean = false

constructor(
private http: Http, 
private _service: NotificationsService,
private database: PouchDBService, 
private zone: NgZone,
) {
  console.log('Service created', _service);

   this.socket = io.connect('http://localhost:3000');
   // var socket = io.connect('http://iontracking2.azurewebsites.net');
    this.socket.on('notifyChanges',function(data){
      console.log('hi')
      _service.success("Notification ", "Flight Moved");
       // document.getElementById('notify').innerHTML=data.msg
    })
}

 public options2 = {
        timeOut: 2800,        
        clickToClose: true,
        maxLength: 0,
        maxStack: 7,
        showProgressBar: false,
        pauseOnHover: true,       
        animate: 'fromRight',
        position: ['top','right']
    };
 onCreate(event) {
        console.log(event);
    }

    onDestroy(event) {
        console.log(event);
    }


  private listener: EventEmitter<any> = new EventEmitter();
  
  static prettyConfirm(title, text, callback) {
    swal({
      title: title,
      text: text,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: "#DD6B55"
    }, callback);
  }

  static mydata

  static static_database_ref


  static displayOnUI(doc){

    console.log('inside Print');
    

      var options = {
        groupOrder: function (a, b) {
          return a.value - b.value;
        },
        groupOrderSwap: function (a, b, groups) {
          var v = a.value;
          a.value = b.value;
          b.value = v;
        },
        onMove: function (item, callback) {
        var title = 'Do you really want to move the item to\n' +
          'start: ' + item.start + '\n' +
          'end: ' + item.end + '?';
     
       AppComponent.prettyConfirm('Move item', title, function (ok) {
        if (ok) {
         

          let id = item.id

          let myarray:string[] = id.split("_");
         
          var dummy = AppComponent.mydata

          for(let aircraft of dummy.aircrafts){
             if(aircraft.nNbr == myarray[0] ){
               let flightindex = myarray[2]
                  

                  aircraft.pFls[flightindex].aGDDt  =""+item.start+""
                  aircraft.pFls[flightindex].aGADt  =""+item.end+""

                  AppComponent.static_database_ref.put('ionTracking',dummy)
             }
          }
        }
        else {
          callback(null); // cancel editing item
        }
      });
    },
        orientation: 'both',
        editable: true,
        groupEditable: true,
        start: new Date("Sat May 29 2015 20:00:00"),
        end: new Date("Mon Jun 1 2015")
      }
      
    var groups = new vis.DataSet(options);
    var items = new vis.DataSet(options)

      var itemCount = 0

      for (let item of doc.aircrafts) {
        var i = 0
        groups.add([
          { "content": item.nNbr, "id": item.nNbr, "value": i++, className: 'openwheel' }
        ])
      }

      var myarray: any = []
      for (let item of doc.aircrafts) {
        if (item.hasOwnProperty('pFls')) {
          myarray.push(item)
        }
      }

      for (let item of myarray) {
        for (let subitem of item.pFls) {
          items.add([
            {
              id:String(item.nNbr)+"_"+String(subitem.fltNbr)+"_"+String(itemCount),
              start: new Date(subitem.aGDDt),
              end: new Date(subitem.aGADt),
              group: item.nNbr,
              className: "openwheel",
              content: subitem.fltNbr,
            },
          ])

         itemCount++
        }
        itemCount=0
      }

    var container = document.getElementById('visualization');

    container.innerHTML = ""
    var timeline = new vis.Timeline(container);
    timeline.setOptions(options);
    timeline.setGroups(groups);
    timeline.setItems(items);

  }

  ngOnInit() {

    AppComponent.static_database_ref = this.database

    this.database.get('ionTracking').then(doc => {
      console.log('inside fetch data successfully if exists')
      AppComponent.mydata = doc
      AppComponent.displayOnUI( AppComponent.mydata)
   //  this.database.sync("http://localhost:4984/ion-tracking");

  this.database.sync("http://54.200.235.10:4984/ion-tracking");
      this.database.getChangeListener().subscribe(data => {      
         for(let i = 0; i < data.change.docs.length; i++) {
                this.zone.run(() => {              
                    AppComponent.mydata = data.change.docs[i]
                    AppComponent.displayOnUI( AppComponent.mydata)
                    this.socket.emit('notify');
                    //document.getElementById('notify').innerHTML='Changes made to Couchbase'
                });   
            } 
      })
  },  
    err => { 
      if (err.status == "404") {
        console.log('inside error 404')
   //    this.database.sync("http://localhost:4984/ion-tracking");
this.database.sync("http://54.200.235.10:4984/ion-tracking");
        this.database.getChangeListener().subscribe(data => {
          for(let i = 0; i < data.change.docs.length; i++) {
                this.zone.run(() => {
                   AppComponent.mydata = data.change.docs[i]
                });
            }
            AppComponent.displayOnUI(AppComponent.mydata)
        });  
      }

      else {
        console.log('inside error else block')
      }
    })
  }

  clickme(){
   if(this.open === false) {
				$('#footerSlideContent').animate({ height: '178px' });
				$(this).css('backgroundPosition', 'bottom left');
				this.open = true;
			} else {
				$('#footerSlideContent').animate({ height: '0px' });
				$(this).css('backgroundPosition', 'top left');
				this.open = false;
			}
  }
}



