import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { MainviewComponent } from './MainView/mainview.component';
import { HeaderComponent } from './header/header.component';
import { NetworkViewComponent } from './network-view/network-view.component';

import { routing } from './app.routing';
import { StationViewComponent } from './station-view/station-view.component';

import { GridViewComponent } from './grid-view/grid-view.component'
import { PouchDBService } from "./services/pouchdb.config";

import {SimpleNotificationsModule, PushNotificationsModule} from 'angular2-notifications';
import { FooterComponent } from './footer/footer.component';




@NgModule({
  declarations: [
    AppComponent,
    MainviewComponent,
    HeaderComponent,
    NetworkViewComponent,
    StationViewComponent, 
    GridViewComponent, FooterComponent,
  
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    SimpleNotificationsModule,
    PushNotificationsModule
  ],
  providers: [PouchDBService],
  bootstrap: [AppComponent]
})
export class AppModule { }



