import { Route , RouterModule , Routes}  from '@angular/router'

import { MainviewComponent } from './MainView/mainview.component'
import { GridViewComponent } from './grid-view/grid-view.component'

import { StationViewComponent } from './station-view/station-view.component'

import { NetworkViewComponent }  from './network-view/network-view.component'


const APP_ROUTES : Routes = [
    {path:'mainview' , component : MainviewComponent},
    {path : 'gridview' , component : GridViewComponent},
    {path : 'stationview' , component : StationViewComponent },
    {path : 'networkview' , component : NetworkViewComponent}
    
]

export const routing = RouterModule.forRoot(APP_ROUTES);


